.contact {
    width:100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", sans-serif;
    }
    
   
    
    .contactBottom {
      display: flex;
      flex-direction: column;
      height: 55%;
      align-items: center;
      margin-top: 0px;
      background-color:black
      
    }
    
    .contactBottom p {
      width: 1200px;
      color: white;
      font-family: "Gill Sans", "Gill Sans MT",
    }
    
    .contact .contactBottom h1 {
      font-weight: 400;
      font-size: 70px;
      color: white;
      height: 30px;
      display: flex;
      justify-content: center;
      transform: translateY(-40px);
    }